// ==========================================================================
// CODE STYLES
// ==========================================================================
//
// We should only need inline styles because everything else will just get thrown
// straight into Codepen.
//

code {
  font-size: 15px;
  position: relative;
  font-family: "Input Mono", monospace;
  background-color: var(--form-bg-color);
  border-radius: 3px;
  display: inline;
  padding: 3px 8px;
  border-radius: 3px;
}
